import { h, Component } from "preact"
import { Router, route } from "preact-router"

import Header from "./header"
import SideNav from "./sidenav"
import NavBottom from "./navBottom"

// Code-splitting is automated for routes
import Store from "../lib/store"
import Home from "../routes/home"
import Comunicados from "../routes/comunicados"
import Clases from "../routes/clases"
import Biblioteca from "../routes/biblioteca"
//import Juegos from "../routes/juegos"
import Login from "../routes/login"

//Mui utils
import Hidden from "@material-ui/core/Hidden"

//Mui Theme
import { createMuiTheme } from "@material-ui/core/styles"
import { ThemeProvider } from "@material-ui/styles"

import UserContext from "../context/UserContext"

const theme = createMuiTheme({
	palette: {
		primary: {
			main: "#01579b",
		},
		secondary: {
			main: "#039be5",
		},
	},
})

const links = ["/", "/clases", "/comunicados", "/biblioteca", "/juegos"]

export default class App extends Component {
	static data = {}
	_isMounted = false
	store = new Store()
	currentUrl = "/login"
	selectedIndex = 0

	constructor() {
		super()
		this.state = {
			isMobileOpen: false,
			user: {},
			selectedIndex: 0,
			isAutenticated: false,
		}
	}

	componentDidMount = () => {
		this._isMounted = true;
		if (this._isMounted) {
			const user = this.store.getUser()
			this.setState({
				user: user,
				selectedIndex: links.indexOf(this.currentUrl),
				isAutenticated: user ? true : false,
			})
			if (!user) {
				route("/login")
			} else {
				if (this.currentUrl == "/login") {
					route("/")
				}
			}
		}
	}

	componentWillUnmount = () => {
		this._isMounted = false
	}

	/** Gets fired when the route changes.
	 *	@param {Object} event		"change" event from [preact-router](http://git.io/preact-router)
	 *	@param {string} event.url	The newly routed URL
	 */
	handleRoute = e => {
		this.currentUrl = e.url
		if (this.state.user == null) {
			this.setState({ isAutenticated: false })
			route("/login")
		} else {
			this.setState({ selectedIndex: links.indexOf(e.url) })
		}
	}

	handleOpenDrawer = e => {
		this.setState({ isMobileOpen: !this.state.isMobileOpen })
	}

	setUser = user => {
		this.setState({ user: user, isAutenticated: user ? true : false })
		if (user) {
			this.store.setUser(user)
			this.store.loadData(user.grade);
			this.store.loadBooks();
			if (this.currentUrl === "/login") {
				this.currentUrl = "/"
				route("/")
			}
		} else {
			this.store.setUser(null)
			this.currentUrl = "/login"
			route("/login")
		}
	}

	selectIndexFromHome = index => {
		this.setState((state, props) => ({ selectedIndex: index }))
	}

	render() {
		const { user, selectedIndex } = this.state
		const { setUser } = this
		return (
			<ThemeProvider theme={theme}>
				<UserContext.Provider value={{ user, setUser }}>
					<div id="app">
						{this.state.isAutenticated ? (
							<Header
								isMobileOpen={this.state.isMobileOpen}
								onToggleDrawer={this.handleOpenDrawer}
							/>
						) : null}
						{this.state.isAutenticated ? (
							<SideNav
								isMobileOpen={this.state.isMobileOpen}
								onToggleDrawer={this.handleOpenDrawer}
								selectedIndex={selectedIndex}
							/>
						) : null}
						<main
							class={
								this.currentUrl == "/login" ? "nomargin" : ""
							}>
							<Router onChange={this.handleRoute}>
								<Home
									path="/"
									onSelect={this.selectIndexFromHome}
								/>
								<Login path="/login" />
								<Clases path="/clases" />
								<Comunicados path="/comunicados" />
								<Biblioteca path="/biblioteca" />
							</Router>{" "}
						</main>
						{this.state.isAutenticated ? (
							<Hidden smUp implementation="css">
								<NavBottom />
							</Hidden>
						) : null}
					</div>
				</UserContext.Provider>
			</ThemeProvider>
		)
	}
}
