import { h } from "preact"
import { useState, useEffect } from "preact/hooks"
import { useContext } from "preact/hooks"
import {
	Card,
	CardHeader,
	CardContent,
	CardActions,
	Button,
	TextField,
} from "@material-ui/core"
import { makeStyles } from "@material-ui/core/styles"
import UserContext from "../../context/UserContext"
import checkStatus from "../../lib/request"

const useStyles = makeStyles(theme => ({
	page: {
		display: "cover",
		marginTop: "-32px",
		padding: 0,
		width: "100%",
		height: "100%",
		background: "url(assets/images/kid-computer.jpg) center center no-repeat"
	},
	container: {
		display: "flex",
		flexWrap: "wrap",
		width: 350,
		margin: `${theme.spacing(0)} auto`,
	},
	loginBtn: {
		marginTop: theme.spacing(2),
		flexGrow: 1,
	},
	header: {
		textAlign: "center",
		background: "#212121",
		color: "#fff",
	},
	card: {
		marginTop: theme.spacing(3),
		opacity: "0.7"
	},
	logo: {
		margin: "auto",
		display: "block",
		marginTop: 10,
		height: 150
	}
}))

function Login() {
	const classes = useStyles()
	const [username, setUsername] = useState("")
	const [password, setPassword] = useState("")
	const [isButtonDisabled, setIsButtonDisabled] = useState(true)
	const [helperText, setHelperText] = useState("")
	const [error, setError] = useState(false)
	const { user, setUser } = useContext(UserContext)

	useEffect(() => {
		if (username.trim() && password.trim()) {
			setIsButtonDisabled(false)
		} else {
			setIsButtonDisabled(true)
		}
	}, [username, password])

	const handleLogin = () => {
		fetch("./assets/data/keys.json")
			.then(checkStatus)
			.then(r => r.json())
			.then(json => {
				let result = false
				json.forEach(function (e) {
					if (e.dni == username && e.key == password) {
						setError(false)
						setHelperText("Login Successfully")
						setUser({
							uid: e.dni,
							name: e.names,
							grade: e.grade,
						})
					} else {
						setError(true)
						setHelperText("Incorrect username or password")
					}
				})
			})
	}

	const handleKeyPress = e => {
		if (e.keyCode === 13 || e.which === 13) {
			isButtonDisabled || handleLogin()
		}
	}
	return (
		<div className={classes.page}>
			<form className={classes.container} noValidate autoComplete="off">
				<img className={classes.logo} src="assets/images/logoJuanPabloII.png" alt="Logo I.E.P. Juan Pablo II" />
				<Card className={classes.card}>
					<CardContent>
						<div>
							<TextField
								error={error}
								fullWidth
								id="username"
								type="email"
								label="Usuario"
								placeholder="Usuario"
								margin="normal"
								onChange={e => setUsername(e.target.value)}
								onKeyPress={e => handleKeyPress(e)}
							/>
							<TextField
								error={error}
								fullWidth
								id="password"
								type="password"
								label="Contrasea"
								placeholder="Contrasea"
								margin="normal"
								helperText={helperText}
								onChange={e => setPassword(e.target.value)}
								onKeyPress={e => handleKeyPress(e)}
							/>
						</div>
					</CardContent>
					<CardActions>
						<Button
							variant="contained"
							size="large"
							color="secondary"
							className={classes.loginBtn}
							onClick={() => handleLogin()}
							disabled={isButtonDisabled}>
							Login
						</Button>
					</CardActions>
				</Card>
			</form>
		</div>
	)
}

export default Login
