import { route } from "preact-router";
import { useState, useContext } from "preact/hooks";
import style from "./style";
import Divider from "@material-ui/core/Divider";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import {
	AccountCircle,
	Home,
	LocalLibrary,
	Description,
	VideogameAsset,
	MenuBook,
} from "@material-ui/icons";
import UserContext from "../../context/UserContext";

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
	root: {
		display: "flex",
	},
	drawer: {
		[theme.breakpoints.up("sm")]: {
			width: drawerWidth,
			flexShrink: 0,
		},
	},
	drawerHeader: {
		display: "flex",
		alignItems: "center",
		justifyContent: "center",
		height: "200px",
		background: "url(assets/images/logoJuanPabloII.png) no-repeat center",
	},
	drawerPaper: {
		width: drawerWidth,
	},
	userName: {
		display: "flex",
		flexDirection: "row",
		alignItems: "center",
		justifyContent: "center",
	},
	content: {
		flexGrow: 1,
		padding: theme.spacing(3),
	},
}));

function SideNav(props) {
	const { container } = props;
	const classes = useStyles();
	const theme = useTheme();
	const { user } = useContext(UserContext); 
	const [selectedIndex, setSelectedIndex] = useState(props.selectedIndex)
	setSelectedIndex(props.selectedIndex);

	const handleListItemClick = (e, index) => {
		setSelectedIndex(index);
		switch (index) {
			case 0:
				route("/");
				break;
			case 1:
				route("/clases");
				break;
			case 2:
				route("/comunicados");
				break;
			case 3:
				route("/biblioteca");
				break;
			case 4:
				route("/juegos");
				break;
		}
	};

	const drawer = (
		<div className={style.sideNav}>
			<div className={classes.drawerHeader} style="text-align: center;"></div>
			<div className={classes.userName}>
				<AccountCircle />
				{(user) ? user.name : ""}
			</div>
			<Divider />
			<List>
				<ListItem
					button
					key="Inicio"
					selected={selectedIndex === 0}
					onClick={event => handleListItemClick(event, 0)}>
					<ListItemIcon>
						<Home color="primary" />
					</ListItemIcon>
					<ListItemText primary="Inicio" />
				</ListItem>
				<ListItem
					button
					key="Clases"
					selected={selectedIndex === 1}
					onClick={event => handleListItemClick(event, 1)}>
					<ListItemIcon>
						<LocalLibrary color="primary" />
					</ListItemIcon>
					<ListItemText primary="Clases" />
				</ListItem>
				<ListItem
					button
					key="Comunicados"
					selected={selectedIndex === 2}
					onClick={event => handleListItemClick(event, 2)}>
					<ListItemIcon>
						<Description color="primary" />
					</ListItemIcon>
					<ListItemText primary="Comunicados" />
				</ListItem>
				<ListItem
					button
					key="Biblioteca"
					selected={selectedIndex === 3}
					onClick={event => handleListItemClick(event, 3)}>
					<ListItemIcon>
						<MenuBook color="primary" />
					</ListItemIcon>
					<ListItemText primary="Biblioteca" />
				</ListItem>
			</List>
		</div>
	);

	return (
		<nav className={classes.drawer} aria-label="clases comunicados biblioteca juegos">
			{/* The implementation can be swapped with js to avoid SEO duplication of links. */}
			<Hidden smUp implementation="css">
				<Drawer
					container={container}
					variant="temporary"
					anchor={theme.direction === "rtl" ? "right" : "left"}
					open={props.isMobileOpen}
					onClose={props.onToggleDrawer}
					classes={{
						paper: classes.drawerPaper,
					}}
					ModalProps={{
						keepMounted: true, // Better open performance on mobile.
					}}>
					{drawer}
				</Drawer>
			</Hidden>
			<Hidden xsDown implementation="css">
				<Drawer
					classes={{
						paper: classes.drawerPaper,
					}}
					variant="permanent"
					open>
					{drawer}
				</Drawer>
			</Hidden>
		</nav>
	);
}

export default SideNav;
