import { useState, useContext } from "preact/hooks"
import { makeStyles } from "@material-ui/core/styles"
import Grid from "@material-ui/core/Grid"
import Fade from "@material-ui/core/Fade"
import DateFnsUtils from "@date-io/date-fns"
import esLocale from "date-fns/locale/es"
import { DatePicker, MuiPickersUtilsProvider } from "@material-ui/pickers"
import Ficha from "../../components/ficha"
import Video from "../../components/video"
import style from "./style"
import { Hidden } from "@material-ui/core"
import CalendarToday from "@material-ui/icons/CalendarToday"

import Store from "../../lib/store"

const localeMap = {
	es: esLocale,
}

const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: "center",
		color: theme.palette.text.secondary,
	},
	mobilePicker: {
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
	},
}))

const Clases = () => {
	const today = new Date();
	const [selectedDate, changeDate] = useState(
	new Date(Date.UTC(
		today.getFullYear(),
		today.getMonth(),
		today.getDate(),
		today.getUTCHours(),
		today.getMinutes(),
		today.getSeconds()
	)));
	const classes = useStyles()
	const store = new Store()
	let data = store.getInfo(
		"clases",
		selectedDate.toISOString().substr(0, 10)
	)
	return (
		<div class={style.clases}>
			<div className={classes.root}>
				<Grid container spacing={3}>
					<Grid item xs={12} sm md={5} lg={4}>
						<MuiPickersUtilsProvider
							utils={DateFnsUtils}
							locale={localeMap.es}>
							<Hidden xsDown>
								<DatePicker
									autoOk
									orientation="portrait"
									variant="static"
									openTo="date"
									value={selectedDate}
									onChange={changeDate}
								/>
							</Hidden>
							<Hidden smUp implementation="css">
								<div className={classes.mobilePicker}>
									<CalendarToday />
									<DatePicker
										value={selectedDate}
										onChange={changeDate}
									/>
								</div>
							</Hidden>
						</MuiPickersUtilsProvider>
					</Grid>
					<Grid item xs={12} sm md={7} lg={8}>
						{data.length > 0 ? (
							data.map(item => {
								return item.type == "doc" ? (
									<Fade in={true}>
										<Ficha url={item.url}/>
									</Fade>
								) : (
									<Fade in={true}>
										<Video videoId={item.videoId} />
									</Fade>
								)
							})
						) : (
							<Fade in={true}>
								<p>
									No hay recursos publicados para la fecha
									seleccionada
								</p>
							</Fade>
						)}
					</Grid>
				</Grid>
			</div>
		</div>
	)
}

export default Clases
