import checkStatus from "./request";

export default class Store {

	loadData = grade => {
		let today = new Date();
		fetch("./assets/data/data.json?v=" + today.toUTCString())
			.then(checkStatus)
			.then(r => r.json())
			.then(json => {
				let data = json.data.filter(function(e){
					return e.grade == grade;
				});
				localStorage.setItem("aulavirtual_data", JSON.stringify(data));
			})
			.catch(e => console.log(e));
	};

	loadBooks = () => {
		fetch("./assets/data/library.json")
			.then(r => {
				return r.json();
			})
			.then(json => {
				this.data = json;
				localStorage.setItem("aulavirtual_library", JSON.stringify(json));
			})
			.catch(e => console.log(e));
	};

	getInfo = (section, date) => {
		const data = JSON.parse(localStorage.getItem("aulavirtual_data"));
		const result = data.filter((el, index, arr) => {
			return (el.date === date && el.section === section);
		});
		return result;
	};

	setUser = user => {
		return localStorage.setItem(
			"aulavirtual_data_user",
			JSON.stringify(user)
		);
	};

	getUser = () => {
		return JSON.parse(localStorage.getItem("aulavirtual_data_user"));
	};

	getBooks = () => {
		return JSON.parse(localStorage.getItem("aulavirtual_library"));
	};

}
